.userContent{
    margin: 5px;
    padding: 5px;
    background-color: #949494;
    border-radius: 5px;
    display: grid;
    grid-template-areas:
    "followed info locale";
    grid-template-columns: 1fr 5fr 1fr;
}

.userContent img{
    width: 90px;
    height: 90px;
    border-radius: 90px;
}
.userFollow{
    grid-area: followed;
    margin: 0 auto;
}

.userFollow div button{
    position: relative;
    display: block;
    margin: 0 auto;
}


.userInfo{
    grid-area: info;
}

.userLocale{
    grid-area: locale;
}


.paginator{
    text-align: center;
    font-size: 18px;
    color: white;
}
.paginator span{
    margin: 10px;
}
.selectedPage{
    font-weight: bold;
    color: #7ddfff;
    font-size: 22px;

}

.paginator button{
    background-color: #7ddfff;
    border: none;
    border-radius: 4px;
    font-weight: 500;
}